import React, { useEffect, useState, useCallback, forwardRef, useImperativeHandle, MouseEvent } from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
  width: 52px;
  height: 52px;
  position: absolute;
  border: 1px solid #61dafb;
  background: #66a7ba;
  z-index: 2;
  top: 13px;
  left: 13px;
`;

type Props = {
  className: string;
  isDrag: boolean;
  checkEvent: (e: React.MouseEvent<HTMLDivElement>, isTdTag?: boolean) => void;
  checkBear: (row: number | null, col: number | null, state: string) => void;
}

type GetAreaSizeType = () => {width: number, height: number};
type SetAreaSizeType = (data: {w: number, h: number}) => void;
type GetDirectionType = () => string;
type SetDirectionType = (dir: string) => void;
type GetAreaPosType = () => {x: number, y: number};
type SetAreaPosType = (data: {x: number, y: number}) => void;

export type DragComponentHandle = {
  getAreaSize: GetAreaSizeType;
  setAreaSize: SetAreaSizeType;
  getDirection: GetDirectionType;
  setDirection: SetDirectionType,
  getAreaPos: GetAreaPosType,
  setAreaPos: SetAreaPosType,
};

const DragComponent = forwardRef<DragComponentHandle, Props>((props, ref) => {
  const { isDrag, checkEvent, checkBear } = props;

  const [dir, setDir] = useState("rightBottom"); // ÏÇ¨Ïö©Ïûê ÎìúÎûòÍ∑∏ Î∞©Ìñ• string
  const [width, setWidth] = useState(52); // ÎìúÎûòÍ∑∏ Ïª¥Ìè¨ÎÑåÌä∏ ÎÑàÎπÑ number
  const [height, setHeight] = useState(56); // ÎìúÎûòÍ∑∏ Ïª¥Ìè¨ÎÑåÌä∏ ÎÜíÏù¥ number
  const [xPos, setXPos] = useState(-1); // ÎìúÎûòÍ∑∏ Ïª¥Ìè¨ÎÑåÌä∏ ÏúÑÏπò x Í∞í number
  const [yPos, setYPos] = useState(-1); // ÎìúÎûòÍ∑∏ Ïª¥Ìè¨ÎÑåÌä∏ ÏúÑÏπò y Í∞í number

  useImperativeHandle(ref, () => ({
    getAreaSize,
    setAreaSize,
    getDirection,
    setDirection,
    getAreaPos,
    setAreaPos,
  }))

  // Í∏∞Îä• : width, height statea Í∞í Î∞òÌôò
  // Ïù∏Ïûê : ÏóÜÏùå
  const getAreaSize: GetAreaSizeType = useCallback(() => {
    return { width, height };
  }, [width, height])

  // Í∏∞Îä• : width, height state Í∞í Î∞òÌôò
  // Ïù∏Ïûê : ÏóÜÏùå
  const setAreaSize: SetAreaSizeType = useCallback(({ w, h }) => {
    /* 
    ÌïúÎ≤àÎçî isDrag Ï≤¥ÌÅ¨ÌïòÍ∏∞
    throttle Í∏∞Îä• ÎïåÎ¨∏Ïóê isDragÍ∞Ä falseÏù∏Îç∞
    default sizeÍ∞Ä ÏïÑÎãå ÌÅ∞ ÏÇ¨Ïù¥Ï¶àÎ°ú Î≥ÄÍ≤ΩÏãúÌÇ§ÎèÑÎ°ù Ìò∏Ï∂úÎêúÎã§ ü•≤.
    */
    if (isDrag) {
      setHeight(h);
      setWidth(w);
    } else {
      setHeight(56);
      setWidth(52);
    }
  }, [isDrag])

  // Í∏∞Îä• : dir state Í∞í Î∞òÌô©
  // Ïù∏Ïûê : ÏóÜÏùå
  const getDirection: GetDirectionType = useCallback(() => {
    return dir;
  }, [dir])

  // Í∏∞Îä• : dir state Í∞í Î≥ÄÍ≤Ω
  // Ïù∏Ïûê : Î≥ÄÍ≤ΩÍ∞í string
  const setDirection: SetDirectionType = useCallback((dir) => {
    setDir(dir);
  }, [])

  // Í∏∞Îä• : xPos, yPos state Í∞í Î∞òÌôò
  // Ïù∏Ïûê : ÏóÜÏùå
  const getAreaPos: GetAreaPosType = useCallback(() => {
    return { x: xPos, y: yPos };
  }, [xPos, yPos])

  // Í∏∞Îä• : xPos, yPos state Í∞í Î≥ÄÍ≤Ω
  // Ïù∏Ïûê : xPos Í∞í x number, yPos Í∞í y number
  const setAreaPos: SetAreaPosType = useCallback(({ x, y }) => {
    setXPos(x);
    setYPos(y);
  }, [])

  // Í∏∞Îä• : dir stateÍ∞íÏóê Îî∞Îùº Í≥ÑÏÇ∞Îêú ÏúÑÏπò Í∞í Î∞òÌôò
  // Ïù∏Ïûê : ÏóÜÏùå
  const computedPos = useCallback(() => {
    // table border-spacing ÎïåÎ¨∏Ïóê
    // 2pxÏî© Ï∞®Ïù¥ÎÇòÎäî Í∞íÏùÑ Í≥ÑÏÇ∞Ìï¥ÏÑú ÎçîÌï¥Ï§ÄÎã§.
    const xDif = 2 * yPos;
    const yDif = 2 * xPos;
    let resultX = yPos * 52 + xDif, resultY = xPos * 56 + yDif;

    let { width, height } = getAreaSize();

    switch (dir) {
      case "rightTop":
        resultY -= (height - 56);
        break;
      case "leftBottom":
        resultX -= (width - 52);
        break;
      case "leftTop":
        resultX -= (width - 52);
        resultY -= (height - 56);
        break;
      default:
        break;
    }

    return `translate(${resultX}px, ${resultY}px)`;
  }, [xPos, yPos, dir, getAreaSize])

  // Ïó≠Ìï† : DragComponent.jsx ÏÇ¨Ïù¥Ï¶à, ÏúÑÏπò Ï¥àÍ∏∞Ìôî
  useEffect(() => {
    /* 
    z-indexÏÜçÏÑ±Ïù¥ 1Ïù¥Í≥† ÏµúÏ†ÅÌôîÎïåÎ¨∏Ïóê opacity ÏÜçÏÑ±ÏùÑ ÏÇ¨Ïö©.
    Îî∞ÎùºÏÑú ÎßàÏö∞Ïä§ Ïù¥Î≤§Ìä∏Í∞Ä Ï†úÎåÄÎ°ú Î∞úÏÉùÌïòÏßÄ ÏïäÍ∏∞ ÎïåÎ¨∏Ïóê
    Î∞òÎìúÏãú ÏÇ¨Ïù¥Ï¶àÏôÄ ÏúÑÏπòÎ•º Ï¥àÍ∏∞Ìôî ÏãúÏºúÏ§òÏïº ÌïúÎã§.
    */
    if (!isDrag) {
      setAreaSize({ h: 56, w: 52 });
      setAreaPos({ x: -1, y: -1 });
    }
  }, [isDrag, setAreaSize, setAreaPos])

  return (
    <Wrapper
      className={`detection ${dir}`}
      style={{
        opacity: isDrag ? 0.5 : 0,
        width: `${width}px`,
        height: `${height}px`,
        transform: computedPos(),
      }}
      onMouseUp={(e) => {
        checkEvent(e);
        checkBear(null, null, "Up");
      }}
      onMouseMove={(e) => {
        checkEvent(e);
      }}
    />
  )
})

export default DragComponent;
